import {Card, CardContent, Typography} from '@material-ui/core'
import React from 'react';
import {Formik} from "formik";
import './FormPurchase.css'
import * as yup from 'yup';
import {modalOperations} from "../../store/modal";
import {useDispatch} from "react-redux";
import {cartOperations} from "../../store/cart";
import NumberFormat from "react-number-format";

const phoneRegExp = /^\s*(?:\+?(\d{1,3}))?([-. (]*(\d{3})[-. )]*)?((\d{3})[-. ]*(\d{2,4})(?:[-.x ]*(\d+))?)\s*$/gm

const FormPurchase = ({cartTotal}) => {
    const validationsSchema = yup.object().shape({
        name: yup.string().min(3, '–ú–∏–Ω–∏–º—É–º 3 —Å–∏–º–≤–æ–ª–∞').max(15, '–ú–∞–∫—Å–∏–º—É–º 15 —Å–∏–º–≤–æ–ª–æ–≤').typeError('–î–æ–ª–∂–Ω–æ –±—ã—Ç—å —Å—Ç—Ä–æ–∫–æ–π').required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        secondName: yup.string().min(3, '–ú–∏–Ω–∏–º—É–º 3 —Å–∏–º–≤–æ–ª–∞').max(15, '–ú–∞–∫—Å–∏–º—É–º 15 —Å–∏–º–≤–æ–ª–æ–≤').typeError('–î–æ–ª–∂–Ω–æ –±—ã—Ç—å —Å—Ç—Ä–æ–∫–æ–π').required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        userAge: yup.number().min(18, '–í–∞–º –Ω–µ—Ç 18 –ª–µ—Ç').typeError('–¢–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä—ã').positive().integer().required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        deliveryAddress: yup.string().required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        mobilePhone: yup.string().matches(phoneRegExp, '–í–≤–µ–¥–∏—Ç–µ –≤–∞–ª–∏–¥–Ω—ã–π –Ω–æ–º–µ—Ä').required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        email: yup.string().email('–í–≤–µ–¥–∏—Ç–µ –≤–µ—Ä–Ω—ã–π "email"').required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        confirmEmail: yup.string().email('–í–≤–µ–¥–∏—Ç–µ –≤–µ—Ä–Ω—ã–π "email"').oneOf([yup.ref('email')], "–ê–¥—Ä–µ—Å–∞ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç").required('–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
    })

    const dispatch = useDispatch()

    return (
        <div>
            <div className="modalOverlay" onClick={() => {
                dispatch(modalOperations.toggleModal("Close"))
            }}/>
            <Card className={'form-purchase'}>
                <CardContent>
                    <Typography variant="h4" className={'formikTitle'}>–û—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ –∑–∞–∫–∞–∑–∞</Typography>
                    <Formik initialValues={{
                        name: '',
                        secondName: '',
                        userAge: '',
                        deliveryAddress: '',
                        mobilePhone: '',
                        email: '',
                        confirmEmail: ''
                    }}
                            validateOnBlur
                            onSubmit={(values) => {
                                console.log(cartTotal)
                                console.log(values)
                                dispatch(cartOperations.checkoutClearCart())
                                dispatch(modalOperations.toggleModal())
                            }}
                            validationSchema={validationsSchema}
                    >
                        {({
                              values,
                              errors,
                              touched,
                              handleChange,
                              handleBlur,
                              isValid,
                              handleSubmit,
                              dirty
                          }) => (
                            <div className={'form'}>

                                <p>
                                    <label htmlFor={"name"}>–ò–º—è</label><br/>
                                    <input type="text"
                                           placeholder="Enter you name"
                                           className={'input'}
                                           name={'name'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.name}
                                    />
                                </p>
                                {touched.name && errors.name && <p className={'error'}>{errors.name}</p>}

                                <p>
                                    <label htmlFor={"secondName"}>–§–∞–º–∏–ª–∏—è</label><br/>
                                    <input type="text"
                                           placeholder="Enter you second name"
                                           className={'input'}
                                           name={'secondName'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.secondName}
                                    />
                                </p>
                                {touched.secondName && errors.secondName &&
                                <p className={'error'}>{errors.secondName}</p>}

                                <p>
                                    <label htmlFor={"userAge"}>–í–≤–µ–¥–∏—Ç–µ –í–∞—à –≤–æ–∑—Ä–∞—Å—Ç</label><br/>
                                    <input type="number"
                                           placeholder="Enter you age"
                                           className={'input'}
                                           name={'userAge'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.userAge}
                                    />
                                </p>
                                {touched.userAge && errors.userAge && <p className={'error'}>{errors.userAge}</p>}

                                <p>
                                    <label htmlFor={"deliveryAddress"}>–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏</label><br/>
                                    <input type="string"
                                           placeholder="Enter delivery address"
                                           className={'input'}
                                           name={'deliveryAddress'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.deliveryAddress}
                                    />
                                </p>
                                {touched.deliveryAddress && errors.deliveryAddress &&
                                <p className={'error'}>{errors.deliveryAddress}</p>}

                                <p>
                                    <label htmlFor={"mobilePhone"}>–í–≤–µ–¥–∏—Ç–µ –º–æ–±–∏–ª—å–Ω—ã–π —Ç–µ–ª–µ—Ñ–æ–Ω</label><br/>
                                    <NumberFormat
                                        // hintText="Some placeholder"
                                        format="+38(###)###-##-##"
                                        placeholder="Enter phone number"
                                        className={'input'}
                                        name={'mobilePhone'}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.mobilePhone}
                                    />
                                </p>
                                {/*<p>*/}
                                {/*    <label htmlFor={"mobilePhone"}>–í–≤–µ–¥–∏—Ç–µ –º–æ–±–∏–ª—å–Ω—ã–π —Ç–µ–ª–µ—Ñ–æ–Ω</label><br/>*/}
                                {/*    <input type="tel"*/}
                                {/*           placeholder="Enter phone number"*/}
                                {/*           className={'input'}*/}
                                {/*           name={'mobilePhone'}*/}
                                {/*           onChange={handleChange}*/}
                                {/*           onBlur={handleBlur}*/}
                                {/*           value={values.mobilePhone}*/}
                                {/*    />*/}
                                {/*</p>*/}
                                {touched.mobilePhone && errors.mobilePhone &&
                                <p className={'error'}>{errors.mobilePhone}</p>}

                                <p>
                                    <label htmlFor={"email"}>–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å 'email'</label><br/>
                                    <input type="email"
                                           placeholder="Enter you email"
                                           className={'input'}
                                           name={'email'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.email}
                                    />
                                </p>
                                {touched.email && errors.email && <p className={'error'}>{errors.email}</p>}

                                <p>
                                    <label htmlFor={"confirmEmail"}>–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ 'email'</label><br/>
                                    <input type="email"
                                           placeholder="Please confirm you email"
                                           className={'input'}
                                           name={'confirmEmail'}
                                           onChange={handleChange}
                                           onBlur={handleBlur}
                                           value={values.confirmEmail}
                                    />
                                </p>
                                {touched.confirmEmail && errors.confirmEmail &&
                                <p className={'error'}>{errors.confirmEmail}</p>}
                                <div className={'formButtons'}>
                                    <button
                                        disabled={!isValid && !dirty}
                                        onClick={handleSubmit}
                                        type={'submit'}
                                    >–û—Ç–ø—Ä–∞–≤–∏—Ç—å
                                    </button>
                                    <button
                                        onClick={() => dispatch(modalOperations.toggleModal())}
                                    >–û—Ç–º–µ–Ω–∏—Ç—å –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ
                                    </button>
                                </div>
                            </div>
                        )}
                    </Formik>
                </CardContent>
            </Card>
        </div>
    );
};

export default FormPurchase;